import md from './doc.md';

export default {
    components: { md },
    data() {
        return {
            activeName: 'first',
            columns: [
                { prop: 'name', label: '姓名' },
                { prop: 'age', label: '年龄' },
                { prop: 'sex', label: '性别' },
                { prop: 'adress', label: '地址' },
            ],
            ajaxData: {
                data: [],
                totalRecords: 10,
            },
            tableData: [
                { name: '测试', age: '12', sex: 'sex1', adress: '武汉' },
                { name: '开发', age: '24', sex: 'sex2', adress: '北京' },
                { name: '产品', age: '48', sex: 'sex3', adress: '上海' },
                { name: '运营', age: '96', sex: 'sex4', adress: '深圳' },
                { name: '运营1', age: '96', sex: 'sex4', adress: '深圳' },
                { name: '运营2', age: '96', sex: 'sex4', adress: '深圳' },
                { name: '运营3', age: '96', sex: 'sex4', adress: '深圳' },
            ]
        }
    },
    methods: {
        ajaxTableData() {
            let { condition, operateType } = this.$refs.selfTable.getParams();
            if (operateType == 2) {
                this.ajaxData.data = this.tableData;
            } else if (condition) {
                this.ajaxData.data = this.tableData.filter(item => item.name.includes(condition));
            } else {
                this.ajaxData.data = this.tableData;
            }
            this.ajaxData.totalRecords = this.ajaxData.data.length;
        }
    },
    created() {
        this.ajaxData.data = this.tableData;
        this.ajaxData.totalRecords = 7;
    },
}